import { useDeferredValue, useEffect, useState } from "react";
import axios from "axios";
import {
  Button,
  Chip,
  Paper,
  Checkbox,
  TextField,
  InputAdornment,
  Typography,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Tooltip,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from "@mui/material";
import { Add, Close, Search, Delete } from "@mui/icons-material";

const fetchTasks = async (keyword) => {
  const { data } = await axios.get("http://localhost:5000/tasks", {
    params: { find: keyword },
  });

  return data;
};

const deleteTask = (id) => axios.delete(`http://localhost:5000/tasks/${id}`);

const markTaskAsDone = (id) =>
  axios.patch(`http://localhost:5000/tasks/${id}/mark`);

const unmarkTaskAsDone = (id) =>
  axios.patch(`http://localhost:5000/tasks/${id}/unmark`);

const NormalApp = () => {
  const [type, setType] = useState("todo");
  const [title, setTitle] = useState("");
  const [deadline, setDeadline] = useState("");
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [tasks, setTasks] = useState([]);
  const [keyword, setKeyword] = useState("");
  const deferredKeyword = useDeferredValue(keyword);

  useEffect(() => {
    let ignore = false;

    fetchTasks(deferredKeyword).then((foundTasks) => {
      if (!ignore) setTasks(foundTasks);
    });

    return () => {
      ignore = true;
    };
  }, [deferredKeyword]);

  const refetchTasks = () => {
    fetchTasks(keyword).then(setTasks);
  };

  const handleAddTask = async () => {
    const payload = { title };

    if (type === "deadline") {
      payload.due_time = deadline;
    } else if (type === "event") {
      payload.start_time = startTime;
      payload.end_time = endTime;
    }

    await axios.post("http://localhost:5000/tasks", { type, payload });

    setTitle("");
    setDeadline("");
    setStartTime("");
    setEndTime("");

    refetchTasks();
  };

  const handleDeleteTask = async (id) => {
    await deleteTask(id);

    refetchTasks();
  };

  const handleMarkTaskAsDone = async (id) => {
    await markTaskAsDone(id);

    refetchTasks();
  };

  const handleUnmarkTaskAsDone = async (id) => {
    await unmarkTaskAsDone(id);

    refetchTasks();
  };

  const shouldDisableAddTaskButton = () => {
    if (type === "deadline") {
      return !title || !deadline;
    } else if (type === "event") {
      return !title || !startTime || !endTime;
    }

    return !title;
  };

  return (
    <div className="p-4 flex flex-col space-y-4 max-w-[600px] m-auto">
      <Paper
        component="form"
        className="px-5 py-4 space-y-3 flex flex-col"
        onSubmit={(e) => {
          e.preventDefault();
          handleAddTask();
        }}
        variant="outlined"
      >
        <Typography variant="h6">Add a task</Typography>

        <FormControl variant="filled" fullWidth>
          <InputLabel id="demo-simple-select-label">Type</InputLabel>

          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            name="type"
            value={type}
            onChange={(e) => setType(e.target.value)}
          >
            <MenuItem value="todo">Todo</MenuItem>
            <MenuItem value="deadline">Deadline</MenuItem>
            <MenuItem value="event">Event</MenuItem>
          </Select>
        </FormControl>

        <TextField
          fullWidth
          name="title"
          label="Title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          type="text"
          variant="filled"
        />

        {type === "deadline" && (
          <TextField
            fullWidth
            label="Deadline"
            name="deadline"
            value={deadline}
            onChange={(e) => setDeadline(e.target.value)}
            type="text"
            variant="filled"
          />
        )}

        {type === "event" && (
          <div className="flex space-x-3">
            <TextField
              fullWidth
              label="Start time"
              name="startTime"
              value={startTime}
              onChange={(e) => setStartTime(e.target.value)}
              type="text"
              variant="filled"
            />

            <TextField
              fullWidth
              label="End time"
              name="endTime"
              value={endTime}
              onChange={(e) => setEndTime(e.target.value)}
              type="text"
              variant="filled"
            />
          </div>
        )}

        <Button
          startIcon={<Add />}
          disableElevation
          type="submit"
          variant="outlined"
          disabled={shouldDisableAddTaskButton()}
        >
          Add task
        </Button>
      </Paper>

      <Paper variant="outlined" className="px-5 py-4 space-y-3">
        <Typography variant="h6">Your tasks ({tasks.length})</Typography>

        {tasks.length > 0 || deferredKeyword ? (
          <>
            <TextField
              size="small"
              fullWidth
              hiddenLabel
              placeholder="Search tasks by title"
              type="text"
              name="keyword"
              value={keyword}
              variant="filled"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Search />
                  </InputAdornment>
                ),
                endAdornment: deferredKeyword && (
                  <InputAdornment position="end">
                    <IconButton onClick={() => setKeyword("")}>
                      <Close />
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              onChange={(e) => setKeyword(e.target.value)}
            />

            {tasks.length > 0 ? (
              <List component={Paper} variant="outlined">
                {tasks.map((task) => {
                  let secondary = "";
                  let color = "success";
                  let label = "Todo";

                  if (task.type === "deadline") {
                    secondary = `By ${task.due_time}`;
                    color = "error";
                    label = "Deadline";
                  } else if (task.type === "event") {
                    secondary = `From ${task.start_time} to ${task.end_time}`;
                    color = "info";
                    label = "Event";
                  }

                  return (
                    <ListItem
                      className="border-0 border-solid border-b last:border-b-0"
                      key={task.id}
                      secondaryAction={
                        <Tooltip title="Delete task">
                          <IconButton
                            onClick={() => handleDeleteTask(task.id)}
                            edge="end"
                            aria-label="Delete"
                            color="error"
                          >
                            <Delete />
                          </IconButton>
                        </Tooltip>
                      }
                      disablePadding
                    >
                      <ListItemButton
                        className={`items-start ${
                          task.done ? "opacity-60 bg-neutral-200" : ""
                        }`}
                        onClick={() => {
                          if (task.done) {
                            handleUnmarkTaskAsDone(task.id);
                          } else {
                            handleMarkTaskAsDone(task.id);
                          }
                        }}
                        dense
                      >
                        <ListItemIcon>
                          <Checkbox
                            edge="start"
                            checked={task.done}
                            tabIndex={-1}
                            disableRipple
                          />
                        </ListItemIcon>

                        <ListItemText
                          className={task.done && "line-through"}
                          secondary={secondary}
                        >
                          <Chip
                            color={color}
                            size="small"
                            variant="outlined"
                            label={label}
                            className="mb-1"
                          />

                          <Typography>{task.title}</Typography>
                        </ListItemText>
                      </ListItemButton>
                    </ListItem>
                  );
                })}
              </List>
            ) : (
              <Typography variant="body1" className="text-center">
                No tasks matched the keyword '{deferredKeyword}'
              </Typography>
            )}
          </>
        ) : (
          <Typography variant="body1" className="text-center">
            New tasks will appear here!
          </Typography>
        )}
      </Paper>
    </div>
  );
};

export default NormalApp;
